import Vue from 'vue';
import Router from 'vue-router';
const _import = require('./_import_' + process.env.NODE_ENV);
// in development env not use Lazy Loading,because Lazy Loading too many pages will cause webpack hot update too slow.so only in production use Lazy Loading

/* layout */
import Layout from '../views/layout/Layout';

Vue.use(Router);

/**
* icon : the icon show in the sidebar
* hidden : if `hidden:true` will not show in the sidebar
* redirect : if `redirect:noredirect` will not redirct in the levelbar
* noDropdown : if `noDropdown:true` will not has submenu in the sidebar
* meta : `{ role: ['admin'] }`  will control the page role
**/
export const constantRouterMap = [
    { path: '/login', component: _import('login/index'), hidden: true },
    { path: '/404', component: _import('404'), hidden: true },
    {
        path: '/',
        component: Layout,
        redirect: '/dashboard',
        hidden: true,
        children: [{ path: 'dashboard', component: _import('dashboard/index') }]
    },
    {
        path: '/modifyPassword',
        hidden: true,
        component: _import('modifyPassword/index')
    },
    {
        path: '/introduction',
        component: Layout,
        redirect: '/introduction/index',
        icon: 'people',
        noDropdown: true,
        children: [{ path: 'index', component: _import('dashboard/index'), name: '' }]
    }
    /* {
        path: '/ueditor',
        component: Layout,
        redirect: '/ueditor/index',
        noDropdown: true,
        children: [
            { path: 'index', name: 'Ueditor', component: _import('editor/editor') }
        ]
    } */
];

export const asyncRouterMap = [
    {
        path: '/cardVoucher',
        component: Layout,
        redirect: 'noredirect',
        icon: 'zujian',
        name: '',
        children: [
            { path: 'cardManage', name: '', icon: 'zonghe', component: _import('cardManage/index') },
            {
                path: '/cardVoucher/cardGBManage',
                name: '',
                icon: 'zonghe',
                component: _import('cardGroupManage/index'),
                redirect: 'noredirect',
                children: [
                    {
                        path: 'GroupOrder',
                        name: '',
                        icon: 'zonghe',
                        component: _import('cardGroupManage/groupOrder')
                    },
                    {
                        path: 'orderDetail',
                        name: '',
                        icon: 'zonghe',
                        hidden: true,
                        component: _import('cardGroupManage/orderDetail')
                    }
                ]
            },
            {
                path: '/cardVoucher/retailOrderManage',
                name: '',
                icon: 'zonghe',
                component: _import('retailOrderManage/index'),
                redirect: 'noredirect',
                children: [
                    {
                        path: 'retailManage',
                        name: '',
                        icon: 'zonghe',
                        component: _import('retailManage/index')
                    },
                    {
                        path: 'retailCardManage',
                        name: '',
                        icon: 'zonghe',
                        component: _import('retailCardManage/index')
                    },
                    {
                        path: 'retailOrder',
                        name: '',
                        icon: 'zonghe',
                        component: _import('retailOrderManage/retailOrder')
                    },
                    {
                        path: 'refundOrder',
                        name: '',
                        icon: 'zonghe',
                        component: _import('retailOrderManage/refundOrder')
                    }
                ]
            }
        ]
    },
    {
        path: '/system',
        component: Layout,
        redirect: 'noredirect',
        name: '',
        icon: 'manage',
        children: [
            { path: 'index', name: '', icon: 'zonghe', component: _import('userManage/index') },
            { path: 'userRole', name: '', icon: 'zonghe', component: _import('userRoleManage/index') },
            { path: 'template', name: '', icon: 'zonghe', component: _import('templateManage/index') },
            { path: 'rulesParams', name: '', icon: 'zonghe', component: _import('ruleParamsManage/index') },
            { path: 'rules', name: '', icon: 'zonghe', component: _import('ruleManage/index') },
            { path: 'dataDic', name: '', icon: 'zonghe', component: _import('dataDicManage/index') },
            { path: 'treeDataDic', name: '', icon: 'zonghe', component: _import('treeDataDicManage/index') },
            { path: 'menu', name: '', icon: 'zonghe', component: _import('menuManage/index') },
            { path: 'fileConfig', name: '', icon: 'zonghe', component: _import('fileConfig/index') },
            {
                path: '/system/log',
                name: '',
                icon: 'zonghe',
                component: _import('log/index'),
                redirect: 'noredirect',
                children: [
                    {
                        path: 'userLogin',
                        name: '',
                        icon: 'zonghe',
                        component: _import('log/userLogin')
                    },
                    {
                        path: 'systemOperate',
                        name: '',
                        icon: 'zonghe',
                        component: _import('log/systemOperate')
                    },
                    {
                        path: 'interface',
                        name: '',
                        icon: 'zonghe',
                        component: _import('log/interface')
                    }
                ]
            },
            { path: 'ftpManage', name: 'FTP', icon: 'zonghe', component: _import('ftpManage/index') },
            {
                path: '/system/taskDispatch',
                name: '',
                icon: 'zonghe',
                component: _import('taskDispatch/index'),
                redirect: 'noredirect',
                children: [
                    {
                        path: 'taskLog',
                        name: '',
                        icon: 'zonghe',
                        component: _import('taskDispatch/taskLog')
                    }
                ]
            },
            {
                path: '/system/userSet',
                name: '',
                icon: 'zonghe',
                component: _import('userSet/index'),
                redirect: 'noredirect',
                children: [
                    {
                        path: 'userInfo',
                        name: '',
                        icon: 'zonghe',
                        component: _import('userSet/userInfo')
                    },
                    {
                        path: 'userPassword',
                        name: '',
                        icon: 'zonghe',
                        component: _import('userSet/userPassword')
                    }
                ]
            },
            { path: 'version', name: '', icon: 'zonghe', component: _import('versionCheck/index') }
        ]
    },
    { path: '*', redirect: '/404', hidden: true }
];

export default new Router({
    // mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes: constantRouterMap
});



